/**
 * MP3
 * http://www.docin.com/p-1261649617.html
 * https://www.codeproject.com/Articles/8295/MPEG-Audio-Frame-Header
 */


import BitStream from '../common/bitstream';

//(bit/s) MEPG1.0 LayerIII
var bitRateMap = {
    '1': 32000,
    '10': 40000,
    '11': 48000,
    '100': 56000,
    '101': 64000,
    '110': 80000,
    '111': 96000,
    '1000': 112000,
    '1001': 128000,
    '1010': 160000,
    '1011': 192000,
    '1100': 224000,
    '1101': 256000,
    '1110': 320000
}
//(Hz) MEPG1.0 LayerIII
var sampleRateMap = {
    '0': 44100,
    '1': 48000,
    '10': 32000
}

var HEADER_MASK = 0xffe0 >> 5; //
var MPEG1 = 3;
var LAYER3 = 1;
var MAX_TAG_OFF = 10 * 1024; //10K

var hasParseVbr = false;

function Header(_bitStream) {
    this.init(_bitStream);
}

var _proto_ = Header.prototype;

/**
 * 
 * @param  {object} arrayBuffer 
 */
_proto_.init = function(_bitStream) {
    this.bitStream = _bitStream;
}
/**
 * 
 * @return object 
 */
_proto_.parseHeader = function() {
    var mask = 0;
    var beginPos = this.bitStream.getBytePos();
    do {
        mask = this.bitStream.getBits(11); //11
        if (mask != HEADER_MASK) {
            if (this.bitStream.isEnd()) {
                break;
            }
            this.bitStream.rewindBits(3);
            continue;
        }
        this.verID = this.bitStream.getBits(2); //MPEG
        this.layer = this.bitStream.getBits(2); //MPEG
        this.protectionBit = this.bitStream.getBits(1); //
        if (!this.frameSync) {
            this.bitStream.rewindBytes(2);
            this.frameSync = this.bitStream.getBits(8).toString(16) + ',' + this.bitStream.getBits(8).toString(16);
            this.frameSync = this.frameSync.toUpperCase(); //
        }
        this.bitrateIndex = this.bitStream.getBitsStr(4);
        this.bitRate = bitRateMap[this.bitrateIndex]; //
        this.sampleRateIndex = this.bitStream.getBitsStr(2);
        this.sampleRate = sampleRateMap[this.sampleRateIndex]; //
        this.paddingBit = this.bitStream.getBits(1); //
        this.privateBit = this.bitStream.getBits(1); //
        this.channelMode = this.bitStream.getBits(2); //
        this.channelModeExtension = this.bitStream.getBits(2); //
        this.copyright = this.bitStream.getBits(1); //
        this.original = this.bitStream.getBits(1); //
        this.emphasis = this.bitStream.getBits(2); //

        if (this.verID != MPEG1 || this.layer != LAYER3) {
            return false;
        }

        this.frameSize = bitRateMap[this.bitrateIndex] * 1152 / 8;
        this.frameSize /= sampleRateMap[this.sampleRateIndex];
        this.frameSize += this.paddingBit; //
        this.sideInfoSize = (this.channelMode == 3) ? 17 : 32; //
        this.duration = 1152 / sampleRateMap[this.sampleRateIndex]; //

        //
        this.mainDataSize = (this.frameSize - 4 - this.sideInfoSize) >> 0; //
        if (this.protectionBit == 0)
            this.mainDataSize -= 2; //CRC
        break;

    } while (this.bitStream.getBytePos() - beginPos < MAX_TAG_OFF);

    if (this.bitStream.getBytePos() - beginPos >= MAX_TAG_OFF || this.bitStream.isEnd()) {
        return false;
    }

    this.endBytePos = this.bitStream.getBytePos();
    if (!hasParseVbr) {
        hasParseVbr = true;
        this.parseVbr();
        if (this.toc) {
            this.totalDuration = this.totalFrames * 1152 / sampleRateMap[this.sampleRateIndex]; //
        }
    }
    return this.bitStream;
}
/**
 * vbr
 */
_proto_.parseVbr = function() {
    var flags = 0;
    var tag = '';

    do {
        tag = String.fromCharCode(this.bitStream.getByte(), this.bitStream.getByte(), this.bitStream.getByte(), this.bitStream.getByte());
        if (tag == 'Xing' || tag == 'Info') { //XingInfo
            this.tocSize = 100; //vbr
            flags = this.bitStream.getBits(32);
            if (flags & 1) {
                this.totalFrames = this.bitStream.getBits(32); //
            }
            if (flags & 2) {
                this.totalBytes = this.bitStream.getBits(32); //
            }
            if (flags & 4) {
                this.toc = []; //vbr
                for (var i = 0; i < this.tocSize; i++) {
                    this.toc[i] = this.bitStream.getByte();
                }
            }
            this.bitStream.setBytePos(this.endBytePos);
            return this.bitStream;
        } else if (tag == 'VBRI') { //VBRI
            this.bitStream.skipBytes(6);
            this.totalBytes = this.bitStream.getBits(32);
            this.totalFrames = this.bitStream.getBits(32);
            this.tocSize = this.bitStream.getBits(16);
            this.bitStream.skipBytes(6);
            this.toc = [];
            for (var i = 0; i < tocSize; i++) {
                this.toc[i] = this.bitStream.getByte();
            }
            this.bitStream.setBytePos(this.endBytePos);
            return this.bitStream;
        } else {
            if (this.bitStream.isEnd()) {
                break;
            }
            this.bitStream.rewindBytes(3);
        }
    } while (tag != 'Xing' && tag != 'Info' && tag != 'VBRI' && this.bitStream.getBytePos() < MAX_TAG_OFF)

    this.bitStream.setBytePos(this.endBytePos);

    return false;
}

export default Header;


